import { TriggerEvent } from '../audioNodeUtils/TriggerEvent';

export class QuantizerNode extends AudioWorkletNode {
   

  constructor(
    context,
    { values = [0], range = 100 },
  ) {
    super(context, 'QuantizerProcessor', {
      numberOfInputs: 0,
      numberOfOutputs: 2,
      outputChannelCount: [1, 1],
      channelCount: 1,
      channelCountMode: 'explicit',
      channelInterpretation: 'discrete',
      processorOptions: { values, range },
    });
    this._position = 0 ;
    this.port.onmessage = ({ data }) => {
      this._position = data;
      this.dispatchEvent(new TriggerEvent());
    };

    this._values = values;
    this._range = range;

    // @ts-ignore
    this.signal = this.parameters.get('signal');
  }

  get position() {
    return this._position;
  }
  get values() {
    return this._values;
  }
  set values(values) {
    this._values = values;
    this.port.postMessage({ values: new Float32Array(values) });
  }
  get range() {
    return this._range;
  }
  set range(range) {
    this._range = range;
    this.port.postMessage({ range });
  }
}
