// import regeneratorRuntime from "regenerator-runtime";
// import "regenerator-runtime/runtime";
import { AudioGraph } from "./audioGraph/AudioGraph";
import { randomGraph } from "./randomGraph";

import styled from "styled-components";

import React, { useState, useMemo, useEffect,useRef } from "react";

import { createRoot } from "react-dom/client";

import { Illustration } from "./components/Illustration"; 
import { GlobalStyle } from "./components/GlobalStyle";
import { PlayButton } from "./components/PlayButton";
import { Glyph } from "./components/Glyph";
import { NetworkActivityIndicator } from "./components/NetworkActivityIndicator";
import { FullscreenButton } from "./components/FullscreenButton";
import { AudioGraphContext } from "./context/AudioGraphContext";

import { lineHeight } from "./style/sizes";
import { useAPIData } from "./hooks/useAPIData";
import { mulberry32 } from "./utlis/mulberry32";
 

const container = document.getElementById("root");



const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 8px 8px 0;
`;

const Col = styled.div`
  display: flex;
  flex-flow: column;
  position: relative;
`;

const CellLink = styled.a`
  &:focus {
    outline: none;
  }
`;

const Row = styled.div`
  padding: 0.5rem 1rem;

  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const RowSide = styled.div`
  display: flex;
  align-items: center;
  gap: 1em;
`;

const Subtext = styled.div`
  color: var(--text-secondary-color);
`;

// parse query params
const { id, v } = window.location.search
  .slice(1)
  .split('&')
  .reduce((memo, param) => {
    const [k, v] = param.split('=');
    memo[k] = v;
    return memo;
  }, {} );

const visualization = v === 'y';
const tokenID = parseInt(id);

const getWidth = () =>  Math.min(window.innerWidth - 16, window.innerHeight - 53);


const Embed = () => {
  const ref = useRef(null);
  const [fullscreen, setFullscreen] = useState(false);
  const [playing, setPlaying] = useState(false);
  const [width, setWidth] = useState(getWidth);
  const [audioGraph, setAudioGraph] = useState(null);
  const [loading, error, metadata] = useAPIData(
    Number.isNaN(tokenID) ? undefined : `audioglyphs/metadata/${tokenID}`,
    [tokenID],
  );
  const graph = useMemo(() => metadata && randomGraph(( 87571254 * tokenID )), [
    metadata,
  ]);

  useEffect(() => {
    if (fullscreen) {
      const exitHandler = () => {
        if (document.fullscreenElement === null) setFullscreen(false);
      };
      document.addEventListener('fullscreenchange', exitHandler);
      return () => {
        document.removeEventListener('fullscreenchange', exitHandler);
      };
    }
    return undefined;
  }, [fullscreen]);
window.rG = randomGraph
  useEffect(() => {
    if (graph) {
      const audioGraph = new AudioGraph(graph);
      audioGraph.ready.then(() => setAudioGraph(audioGraph));
    }
  }, [graph]);

  useEffect(() => {
    window.addEventListener('resize', () => {
      setWidth(getWidth());
    });
  }, []);

  const context = audioGraph?.context ;

  const play = () => {
    setPlaying(true);
    context?.resume();
  };

  const pause = () => {
    setPlaying(false);
    context?.suspend();
  };

  return (
    <>
      <GlobalStyle />
      <Container ref={ref}>
        {metadata && graph && audioGraph ? (
          <AudioGraphContext.Provider
            value={{
              audioGraph,
              graph,
              metadata,
              playing,
              play,
              pause,
              stop: () => {},
            }}
          >
            <Col style={{ width }}>
              {visualization && (
                
                  <Illustration
                    graph={graph}
                    audioGraph={audioGraph}
                    playing={playing}
                  />
                
              )}
              <Row>
                <RowSide>
                  <PlayButton
                    size={lineHeight}
                    playing={playing}
                    toggle={playing ? pause : () => play()}
                  />
                  <div>
                  <a href={`https://www.b16nary.xyz`}>SYNTO RIPPER</a>
                    <Subtext>#{tokenID}</Subtext>
                  </div>
                </RowSide>
                <RowSide>
                  
                  <FullscreenButton
                    size={lineHeight}
                    fullscreen={fullscreen}
                    toggle={() => {
                      if (fullscreen) {
                        document.exitFullscreen();
                        setFullscreen(false);
                      } else {
                        ref.current?.requestFullscreen();
                        setFullscreen(true);
                      }
                    }}
                  />
                </RowSide>
              </Row>
            </Col>
          </AudioGraphContext.Provider>
        ) : (
          <NetworkActivityIndicator
            pending={false}
            active={loading}
            error={!!error}
          />
        )}
      </Container>
    </>
  );
};


const root = createRoot(container);
root.render(<Embed />);
