  function baseGraph1({
  seed: e,
  name: t,
  nodes: r,
  edges: n
}){

  const o = {},
      a = {};
   Object.values(r).forEach((e => {
      o[e.id] = [];
       a[e.id] = [];
  })) 
  Object.values(n).forEach((({
      id: e,
      from: {
          node: t
      },
      to: {
          node: r
      }
  }) => {
      o[r].push(e); a[t].push(e)
  }))
  return {
      seed: e,
      name: t,
      nodes: r,
      edges: n,
      incomingEdges: o,
      outgoingEdges: a
  }
}
 export const baseGraph =  baseGraph1(setNamesAsIDs(baseGraphForStorage));
