import {
  TriggerEvent
} from '../audioNodeUtils/TriggerEvent';

export class TriggeredSamplerNode extends AudioWorkletNode {

  constructor(
    context, {
      clock = 0,
      detune = 0,
      buffer = null,
    }
  ) {
    super(context, 'TriggeredSamplerProcessor', {
      numberOfInputs: 0,
      numberOfOutputs: 1,
      outputChannelCount: [1],
      channelCount: 1,
      channelCountMode: 'explicit',
      channelInterpretation: 'discrete',
      processorOptions: {
        clock,
        detune,
        buffer
      },
    });
    this.port.onmessage = () => {
      this.dispatchEvent(new TriggerEvent());
    };

    this._buffer = buffer;

    // @ts-ignore
    this.clock = this.parameters.get('clock');
    // @ts-ignore
    this.detune = this.parameters.get('detune');
  }

  get buffer() {
    return this._buffer;
  }
  set buffer(buffer) {
    this._buffer = buffer;
    if (buffer !== null) {
      this.port.postMessage(buffer.getChannelData(0));
    } else {
      this.port.postMessage(null);
    }
  }
}