const { sin, cos, pow, PI, floor } = Math;

export const createVarient = (graph) => {
  // tokenID = id;

  // const graph = randomGraph(87571254 * tokenID)
  const random = mulberry32(graph.seed);
  const rand = (min, max) => min + random() * (max - min);
  const hue = rand(0, 360);

  const bC = [rand(0, 360), rand(50, 100), rand(20, 100)];
  const lC = [rand(0, 360), rand(60, 100), rand(70, 100)];
  const buC = [rand(0, 360), rand(60, 100), 50];

  const backgroundColor = new Color(...hsluvToRgb(bC));
  const lightColor = new Color(...hsluvToRgb(lC));
  const buildingColor = new Color(...hsluvToRgb(buC));

  const speed = 0.8 + pow(random(), 5) * 2;
  const h1 = rand(0, 100) < 40 ? -1 : 1;
  const h2 = rand(0, 100) < 20 ? new Color(0) : backgroundColor;

  const geomenteyType = rand(0, 100) < 30 ? 0 : 1;
 
  const torusRadius = rand(150, 200);
  const torusDiameter = rand(25, 50);
  const rings = 45 * rand(1, 4);
  const detail = rand(10, 50);

  return {
   
    backgroundColor,
    lightColor,
    buildingColor,
    speed,
    h1,
    h2,
    geomenteyType,
    bC,
    lC,
    buC,
    torusRadius,
    torusDiameter,
    detail,
    rings,
  };
};

export const createCityScene = (graph, audioGraph, renderer) => {
  const sizes = {
    width: window.innerWidth,
    height: window.innerHeight,
  };

  const nodes = graph.nodes;

  let {
    // tokenID,

    backgroundColor,
    lightColor,
    buildingColor,
    speed,
    h1,
    h2,
    geomenteyType,
    bC,
    lC,
    buC,
    torusRadius,
    torusDiameter,
    detail,
    rings,
  } = createVarient(graph);

  const cUniform = {
    iTime: {
      value: 0,
    },
    iResolution: {
      value: new Vector2(window.innerWidth, window.innerHeight),
    },
    uGroup: {
      value: 1,
    },
  };

  const eve = () => {
    const triggerNodes = [];

    if (graph.seed === graph.seed) {
      if (nodes.BD_GAN.audioNode.params.gain > 0)
        triggerNodes.push(audioGraph.audioNodes.BD_RTM);
      if (nodes.SNR_GAN.audioNode.params.gain > 0) ;
      triggerNodes.push(audioGraph.audioNodes.SNR_RTM);
      triggerNodes.push(audioGraph.audioNodes.AD);
      triggerNodes.push(audioGraph.audioNodes.HH_RTM);

      const listeners = triggerNodes.map((node, i) => {
        const listener = (e) => {
          cUniform.uGroup.value += 0.1;
        };
        node.addEventListener("trigger", listener);
        return listener;
      });

      return () => {
        listeners.forEach((listener, i) =>
          triggerNodes[i].removeEventListener("trigger", listener)
        );
      };
    }
    return undefined;
  };
  eve();

  const resources = [];

  const scene = new Scene();

  scene.background = new Color(backgroundColor);

  const camera = new PerspectiveCamera(60, 1, 0.1, 10000);

  scene.add(camera);

  const light = new PointLight(lightColor, 2, 150);
  scene.add(light);
  resources.push(light);

  const count = 9000;

  const geometry = [BoxGeometry, OctahedronGeometry];
  const mesh = new InstancedMesh(
    new geometry[geomenteyType](2, 2),
    new MeshLambertMaterial(),
    count
  );
  scene.add(mesh);
  mesh.material.onBeforeCompile = (shader) => {
    shader.uniforms = {
      ...shader.uniforms,
      ...cUniform,
    };
    shader.vertexShader = shader.vertexShader.replace(
      "void main() {",
      `
  attribute float aIDs;
  varying float vIDs;
  void main() {
    vIDs = aIDs;

  `
    );
    shader.fragmentShader = shader.fragmentShader.replace(
      "void main() {",
      `
  uniform float uGroup;    
  uniform float iTime;
  varying float vIDs;
  void main() {`
    );

    shader.fragmentShader = shader.fragmentShader.replace(
      "vec4 diffuseColor = vec4( diffuse, opacity );",
      `
  vec4 diffuseColor = vec4( diffuse , opacity ) ;
  
   
  `
    );
  };

  let kim = 0;

  const ids = [];

  function createTorus() {
    for (let i = 0; i < rings; i++) {
      let u = (i / rings) * Math.PI * 2;
      let ring = new Object3D();
      ring.position.x = torusRadius * Math.cos(u);
      ring.position.z = torusRadius * Math.sin(u);

      for (let j = 0; j < detail; j++) {
        let v = (j / detail) * Math.PI * 2;

        let x = torusDiameter * Math.cos(v) * Math.cos(u);
        let y = torusDiameter * Math.sin(v);
        let z = torusDiameter * Math.cos(v) * Math.sin(u);

        let size = Math.random() * 5 + 0.1;
        let cube = new Object3D();

        cube.scale.set(size, size, size);
        cube.position.set(x, y, z);

        let rotation = (Math.random() - 0.5) * Math.PI * 4;
        cube.rotation.set(rotation, rotation, rotation);
        ring.add(cube);

        ring.updateWorldMatrix();
        cube.updateWorldMatrix();

        scene.updateWorldMatrix();
        // ids.push(kim);
        mesh.setColorAt(kim, buildingColor);
        mesh.setMatrixAt(kim, cube.matrixWorld);

        kim++;
        mesh.instanceMatrix.needsUpdate = true;
      }
    }
  }
  createTorus();

  // mesh.geometry.setAttribute(
  //   "aIDs",
  //   new InstancedBufferAttribute(new Float32Array(kim), 1)
  // );
  const material = new MeshStandardMaterial({
    color: buildingColor,
    dithering: true,
  });
  resources.push(material);

  const composer = new EffectComposer(renderer);
  const renderpass = new RenderPass(scene, camera);
  const glitch = new GlitchPass();
  composer.addPass(renderpass);
  // composer.addPass(glitch)

  const shaderpass = new ShaderPass(
    new ShaderMaterial({
      uniforms: {
        tDiffuse: {
          value: null,
        },
        ...cUniform,
      },
      vertexShader: `
    varying vec2 vUv;
    void main(){
        vUv = uv ; 
        gl_Position  = projectionMatrix * modelViewMatrix * vec4(position,1.);
    }`,
      fragmentShader: `
    #define iTime mod(uGroup,1.)
    varying vec2 vUv ;
    uniform sampler2D tDiffuse;
    // uniform float iTime;
    uniform float iResolution;
    uniform float uGroup;
    
    #define iTime mod(uGroup,1.)

    vec2 curve(vec2 uv)
    {
        uv = (uv - 0.5) * 2.0;
        uv *= 1.1;	
        uv.x *= 1.0 + pow((abs(uv.y) / 5.0), 2.0);
        uv.y *= 1.0 + pow((abs(uv.x) / 4.0), 2.0);
        uv  = (uv / 2.0) + 0.5;
        uv =  uv *0.92 + 0.04;
        return uv;
    }



    vec4  getCurve()
{
    vec2 q = vUv;
    vec2 uv = q;
    uv = curve( uv );
    vec3 oricol = texture2D( tDiffuse , vec2(q.x,q.y) ).xyz;
    vec3 col;
	float x =  sin(0.3*iTime+uv.y*21.0)*sin(0.7*iTime+uv.y*29.0)*sin(0.3+0.33*iTime+uv.y*31.0)*0.0017;

    col.r = texture(tDiffuse,vec2(x+uv.x+0.001,uv.y+0.001)).x+0.05;
    col.g = texture(tDiffuse,vec2(x+uv.x+0.000,uv.y-0.002)).y+0.05;
    col.b = texture(tDiffuse,vec2(x+uv.x-0.002,uv.y+0.000)).z+0.05;
    col.r += 0.08*texture(tDiffuse,0.75*vec2(x+0.025, -0.027)+vec2(uv.x+0.001,uv.y+0.001)).x;
    col.g += 0.05*texture(tDiffuse,0.75*vec2(x+-0.022, -0.02)+vec2(uv.x+0.000,uv.y-0.002)).y;
    col.b += 0.08*texture(tDiffuse,0.75*vec2(x+-0.02, -0.018)+vec2(uv.x-0.002,uv.y+0.000)).z;

    col = clamp(col*0.6+0.4*col*col*1.0,0.0,1.0);

    float vig = (0.0 + 1.0*16.0*uv.x*uv.y*(1.0-uv.x)*(1.0-uv.y));
	col *= vec3(pow(vig,0.3));

    col *= vec3(0.95,1.05,0.95);
	col *= 2.8;

	float scans = clamp( 0.35+0.35*sin(3.5*iTime+uv.y*vUv.y*1.5), 0.0, 1.0);
	
	float s = pow(scans,1.7);
	col = col*vec3( 0.4+0.7*s) ;

    col *= 1.0+0.01*sin(110.0*iTime);
	if (uv.x < 0.0 || uv.x > 1.0)
		col *= 0.0;
	if (uv.y < 0.0 || uv.y > 1.0)
		col *= 0.0;
	
	col*=1.0-0.65*vec3(clamp((mod(vUv.x, 2.0)-1.0)*2.0,0.0,1.0));
	
    float comp = smoothstep( 0.1, 0.9, sin(iTime) );
 
	// Remove the next line to stop cross-fade between original and postprocess
//	col = mix( col, oricol, comp );

    return  vec4(col,1.0);
}

 
    void main(){

    

        gl_FragColor =getCurve()*.8;
        
        
    }`,
    }),
    "tDiffuse"
  );

  composer.addPass(shaderpass);

  const update = (time, delta) => {
    time *= speed;
    const uTime = time * speed * h1;
    light.position.x = Math.cos(uTime) * torusRadius;
    light.position.z = Math.sin(uTime) * torusRadius;
    cUniform.iTime.value += 0.1;
    camera.position.x = Math.cos(time - 0.5) * torusRadius;
    camera.position.z = Math.sin(time - 0.5) * torusRadius;
    camera.position.y = Math.sin(time - 0.5) * sin(uTime);
    camera.lookAt(light.position);
    // camera.rotation.x = PI   ;
    // camera.rotation.y = cos(uTime);
  };
  update(0.5, 0);

  return {
    scene,
    camera,
    update,
    composer,
    dispose() {
      resources.forEach((r) => r.dispose());
    },
  };
};
