import { ParamType } from "../types/AudioNodeDescriptions";

//  import { ParamType, NodeDescription } from '';
// NodeDescription


export function getAudioParamNames(description) {
  return Object.entries(description.params)
    .filter(([_, { type }]) => type === ParamType.AudioParam)
    .map(([name]) => name);
}
