import React, { FC } from 'react';
import { theme } from '../style/colors';
import { lineHeight } from '../style/sizes';
// import { lineHeight, theme } from '../style/colors';
 

export const FullscreenButton = ({
  size = lineHeight,
  fullscreen,
  toggle,
}) => {
  return (
    <svg
      tabIndex={1}
      onClick={toggle}
      onKeyDown={e => {
        if (e.key === 'Enter') toggle();
      }}
      width={size}
      height={size}
      viewBox="0 0 16 16"
    >
      <title>{fullscreen ? 'end fullsecreen' : 'fullscreen'}</title>
      {fullscreen ? (
        <>
          <rect
            x={1}
            y={1}
            width={14}
            height={14}
            fill={theme['--text-color']}
          />
          <rect
            x={1}
            y={6}
            width={14}
            height={4}
            fill={theme['--background-color']}
          />
          <rect
            x={6}
            y={1}
            width={4}
            height={14}
            fill={theme['--background-color']}
          />
          <rect
            x={1}
            y={1}
            width={3}
            height={3}
            fill={theme['--background-color']}
          />
          <rect
            x={12}
            y={1}
            width={3}
            height={3}
            fill={theme['--background-color']}
          />
          <rect
            x={1}
            y={12}
            width={3}
            height={3}
            fill={theme['--background-color']}
          />
          <rect
            x={12}
            y={12}
            width={3}
            height={3}
            fill={theme['--background-color']}
          />
        </>
      ) : (
        <>
          <rect
            x={1}
            y={1}
            width={14}
            height={14}
            fill={theme['--text-color']}
          />
          <rect
            x={1}
            y={6}
            width={14}
            height={4}
            fill={theme['--background-color']}
          />
          <rect
            x={6}
            y={1}
            width={4}
            height={14}
            fill={theme['--background-color']}
          />
          <rect
            x={3}
            y={3}
            width={10}
            height={10}
            fill={theme['--background-color']}
          />
        </>
      )}
    </svg>
  );
};
