import React, {  useRef, useEffect } from 'react';
import styled from 'styled-components';
 import { AudioGraph } from '../audioGraph/AudioGraph';
import { renderer, createCityScene } from '../visualization/index';

 
export const Container = styled.div`
  aspect-ratio: 1;
  width: 100%;
  background: #001;
`;

export const Illustration = ({
  graph,
  audioGraph,
  audioElement,
  onClick,
}) => {
  const containerRef = useRef(null);

  useEffect(() => {
    if (containerRef.current === null) return;

    containerRef.current.appendChild(renderer.domElement);

    const updateSize = () => {
      if (containerRef.current) {
        const size = Math.min(
          containerRef.current.offsetWidth,
          containerRef.current.offsetHeight,
        );
        renderer.setSize(size, size);
      }
    };
    window.addEventListener('resize', updateSize);
    updateSize();

    return () => {
      containerRef.current?.removeChild(renderer.domElement);
      window.removeEventListener('resize', updateSize);
    };
  }, [renderer]);

  useEffect(() => {
    if (audioGraph === null  ) return;
    // if(audioGraph.context.state == 'suspended' ) return ;
    const { scene, camera, composer , dispose, update } = createCityScene(graph,audioGraph,renderer);
    window.scene = scene;
    window.camera = camera;
    let time = 0.6;
    let initTime = 0;
    let prevTime = initTime;
    let live = true;
    const animate = () => {
      if (!live) return;
       time += 0.1;
      const iTime =  audioGraph?.context?.currentTime ?? 0.5;
      const delta = iTime - prevTime;
      prevTime = iTime;
      composer.render();
      // renderer.render(scene, camera);
       update(iTime, delta);

      requestAnimationFrame(animate);
    };
    animate();

    return () => {
      live = false;
      dispose();
    };
  }, [graph.seed, audioGraph || audioElement]);

  return <Container ref={containerRef} onClick={onClick} />;
};
